const LAll2 = () =>{
    let x = ''
    let w = window.innerWidth*0.15
    let h = window.innerHeight*0.185
    //["Nhạc thánh ca K'HO", <iframe className='img-slide1' data-aos={"fade-left"} data-aos-duration="100"  width={w} height={h} src="https://www.youtube.com/embed/8LGPDjfVPGg" title="YouTube video player" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>],
    //             ["Tuyển chọn nhạc thánh tiếng K'HO hay", <iframe className='img-slide1' data-aos={"fade-left"} data-aos-duration="100"  width={w} height={h} src="https://www.youtube.com/embed/REWoNT_rpMY" title="YouTube video player" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>]
    let a = [['vid',"Dân ca K'HO Lâm Đồng", <iframe style={{borderRadius:'10px'}} className='img-slide1' data-aos={"fade-left"} data-aos-duration="100"  width={w} height={h} src="https://www.youtube.com/embed/M7nIaUMh4X0" title="YouTube video player" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>,"https://www.youtube.com/watch?v=M7nIaUMh4X0"],
            ['vid',"Đồng bào dân tộc K'Ho Kon Pang",<iframe style={{borderRadius:'10px'}} className='img-slide1' data-aos={"fade-left"} data-aos-duration="100"  width={w} height={h} src="https://www.youtube.com/embed/4bHrrIq_ukc" title="YouTube video player" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>,"https://www.youtube.com/watch?v=4bHrrIq_ukc"],

    ]
    a = a.map((i)=>{
        i[0]=='vid'?

        x=    <div>
                <div className='types' data-aos="fade-right" data-aos-duration="100"  data-aos-easing="ease-in-sine">{i[1]}</div>
                <div style={{borderRadius:'10px'}} data-aos="fade-right"  data-aos-easing="ease-in-sine" data-aos-duration="100">{i[2]}</div>
                <div data-aos="fade-right"  data-aos-easing="ease-in-sine" data-aos-duration="100">{LinkT(i[3])}</div>
            </div>:
        x=  <div>
                <div className='types' data-aos="fade-right" data-aos-duration="100"  data-aos-easing="ease-in-sine" onClick={()=>LinkImg(i[3])}>{i[1]}</div>
                <img style={{borderRadius:'10px'}} className='img-slide1' src={i[2]} alt={''} width={w} height={h} data-aos="fade-right"  data-aos-easing="ease-in-sine" data-aos-duration="100" onClick={()=>LinkImg(i[3])}/>
                <div data-aos="fade-right"  data-aos-easing="ease-in-sine" data-aos-duration="100">{LinkT(i[3])}</div>
            </div>
        return x


    })
    return a


}
